<template>
    <div class="header">
      <div class="content-wrapper">
          <div class="avatar">
              <img width="64px" height="64px" :src="seller.avatar">
          </div>
          <div class="content">
              <div class="title">     
                  <span class="name">{{seller.name}}</span>
              </div>
              <div class="description">
                  {{seller.description}}{{seller.deliveryTime}}分钟送达
              </div>
              <div v-if="seller.supports" class="support">
                <span class="icon" :class="classMap[seller.supports[0].type]"></span>
                <span class="text">{{seller.supports[0].description}}</span>
              </div>
          </div>
          <div class="collection-wrapper">
            <collection :BS="BS"></collection>
          </div>
          <div v-if="seller.supports" class="support-count" @click="showDetail">
            <span class="count">{{seller.supports.length}}个活动</span>
            <i class="icon-keyboard_arrow_right">></i>
          </div>
      </div>
      <div class="bulletin-wrapper">
        <span class="bulletin-title"></span>
        <span class="bulletin-text">{{seller.bulletin}}</span>
      </div>
      <div class="background">
        <img :src="seller.avatar" width="100%" height="100%">
      </div>
      <div v-show="detailShow" class="detail" transition="fade">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
            </div>
            <div class="detail-content">
              <span>起送 ￥{{seller.minPrice}}</span>
              <span>配送 ￥{{seller.deliveryPrice}}</span>
              <span>{{seller.deliveryTime}}分钟</span>
            </div>
            <div class="time">
              <span>配送时间：10.00-22.59</span>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li class="support-item" v-for="(item,index) in seller.supports">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close" @click="hideDetail">
          <span class="icon-close">x</span>
        </div>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import star from 'components/star/star.vue';
    import collection from 'components/collection/collection.vue';

    export default {
      props: {
        seller: {
          type: Object
        }
      },
      data() {
        return {
          BS: false,
          detailShow: false
        };
      },
      methods: {
        showDetail() {
          this.detailShow = true;
        },
        hideDetail() {
          this.detailShow = false;
        }
      },
      created() {
        this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      },
      components: {
        star,
        collection
      }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin";

    .header
      position: relative
      color：#fff
      .content-wrapper
        position: relative
        padding: 24px 12px 18px 24px
        font-size: 0
        color: #fff
        .avatar
          display: inline-block
          vertical-align: top
          img
            border-radius: 2px
        .content
          display: inline-block
          margin-left: 16px
          font-size: 14px
          .title
            margin: 2px 8px 0 0
            font-weight: 100
            .brand
              display: inline-block
              vertical-align: top
              width: 30px
              height: 18px
              bg-image('brand')
              background-size: 30px 18px
              background-repeat: no-repeat
            .name
              margin-left: 6px
              font-size: 16px
              line-height: 18px
          .description
            margin-bottom: 10px
            line-height: 12px
            font-size: 12px
          .support
            .icon
              display: inline-block
              width: 12px
              height: 12px
              margin-right: 4px
              background-size: 12px 12px
              background-repeat: no-repeat
              &.decrease
                bg-image('decrease_1')
              &.discount
                bg-image('discount_1')
              &.guarantee
                bg-image('guarantee_1')
              &.invoice
                bg-image('invoice_1')
              &.special
                bg-image('special_1')
            .text
              font-size: 12px
              line-height: 12px
        .collection-wrapper
            width: 50px
            height: 70px
            float: right
            border-left: 1px solid grey
        .support-count
          position: absolute
          right: 12px
          bottom: -30px
          padding: 0 8px
          line-height: 24px
          text-align: center
          .count
            font-size: 10px
          .icon-keyboard_arrow_right
            line-height: 24px
            margin-left: 2px
            font-size: 10px
      .bulletin-wrapper
        height: 28px
        line-height: 28px
        padding: 0 22px 0 12px
        white-space: nowrap
        overflow: hidden
        text-overflow: ellipsis
        color: #fff
        .bulletin-title
          display: inline-block
          margin-top: 8px
          width: 22px
          height: 12px
          bg-image('bulletin')
          background-size: 22px 12px
          background-repeat: no-repeat
        .bulletin-text
          display: inline-block
          vertical-align: top
          font-size: 10px
      .background
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        z-index: -1
        filter: blur(10px)
      .detail
        position: fixed
        z-index: 100
        top: 0
        left: 0
        width: 100%
        height: 100%
        overflow: auto
        backdrop-filter: blur(10px)
        opacity: 1
        background: rgba(7, 17, 27, 0.8)
        color: #fff
        &.fade-enter-active, &.fade-leave-active
          transition: all 0.5s
        &.fade-enter, &.fade-leave-active
          opacity: 0
          background: rgba(7, 17, 27, 0)
        .detail-wrapper
          width: 100%
          min-height: 100%
          .detail-content
            margin: 10px 15px 0 0
            text-align: center
            font-weight: 100
          .time
            margin: 10px 15px 0 0
            text-align: center
          .detail-main
            margin-top: 64px
            padding-bottom: 64px
            .name
              line-height: 16px
              text-align: center
              font-size: 16px
              font-weight: 700
            .star-wrapper
              margin-top: 18px
              padding: 2px 0
              text-align: center
            .title
              display: flex
              width: 80%
              margin: 28px auto 24px auto
              .line
                flex: 1
                position: relative
                top: -6px
                border-bottom: 1px solid rgba(255, 255, 255, 0.2)
              .text
                padding: 0 12px
                font-weight: 700
                font-size: 14px
            .supports
              width: 80%
              margin: 0 auto
              .support-item
                padding: 0 12px
                margin-bottom: 12px
                font-size: 0
                &:last-child
                  margin-bottom: 0
                .icon
                  display: inline-block
                  width: 16px
                  height: 16px
                  vertical-align: top
                  margin-right: 6px
                  background-size: 16px 16px
                  background-repeat: no-repeat
                  &.decrease
                    bg-image('decrease_2')
                  &.discount
                    bg-image('discount_2')
                  &.guarantee
                    bg-image('guarantee_2')
                  &.invoice
                    bg-image('invoice_2')
                  &.special
                    bg-image('special_2')
                .text
                  line-height: 16px
                  font-size: 12px
            .bulletin
              width: 80%
              margin: 0 auto
              .content
                padding: 0 12px
                line-height: 24px
                font-size: 12px
        .detail-close
          position: relative
          width: 32px
          height: 32px
          margin: -100px auto 0 auto
          clear: both
          font-size: 32px
          color: #fff
</style>



